import React, { useState } from "react";
import "./App.css";

function Todo({ todo, index, completeTodo, removeTodo }) {
  return (
    <div className='todo'>
      <p
        className='todo_text'
        style={{ textDecoration: todo.isCompleted ? "line-through" : "none" }}>
        <div className='todo_buttons'>
          <button onClick={() => completeTodo(index)}>Complete</button>
          <button onClick={() => removeTodo(index)}>x</button>
        </div>
        {todo.text}
      </p>
    </div>
  );
}

function TodoForm({ addTodo }) {
  const [value, setValue] = useState("");

  const handleSubmit = e => {
    e.preventDefault();
    if (!value) return;
    addTodo(value);
    setValue("");
  };

  return (
    <form onSubmit={handleSubmit}>
      <input
        type='text'
        className='input'
        placeholder='Новая задача'
        value={value}
        onChange={e => setValue(e.target.value)}
      />
      <button className='addButton'>Add</button>
    </form>
  );
}

function App() {
  let [todos, setTodos] = useState([
    {
      text: "Сделать тудушку",
      isCompleted: false
    },
    {
      text: "Залить в репу на гит",
      isCompleted: false
    },
    {
      text:
        "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Delectus rerum, vel autem voluptas similique nisi veritatis iste tempora fugiat tenetur voluptatem tempore facere nemo maiores ipsum officia nam! Aperiam incidunt molestiae voluptates modi sit illo, quod quis provident assumenda accusantium consequuntur, dicta ea ad, architecto nam doloremque quibusdam officiis veritatis error quisquam enim repudiandae quia necessitatibus suscipit. Voluptatem modi eum itaque! Explicabo, inventore accusantium! Sunt esse magnam nobis obcaecati aspernatur amet repellat id excepturi ratione dolorum deleniti quas officiis neque, totam iste ut velit, ex perferendis consequuntur. Beatae deserunt consequatur amet? Voluptatem doloribus, obcaecati accusamus ex magni quod voluptates omnis.      ",
      isCompleted: false
    }
  ]);

  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
  };

  const completeTodo = index => {
    const newTodos = [...todos];

    newTodos[index].isCompleted !== true
      ? (newTodos[index].isCompleted = true)
      : (newTodos[index].isCompleted = false);

    setTodos(newTodos);
  };

  const removeTodo = index => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodos(newTodos);
  };

  return (
    <div className='app'>
      <div className='todo-list'>
        <TodoForm addTodo={addTodo} />

        {todos.map((todo, index) => (
          <Todo
            key={index}
            index={index}
            todo={todo}
            completeTodo={completeTodo}
            removeTodo={removeTodo}
          />
        ))}
      </div>
    </div>
  );
}

export default App;
